header {
  width: 100vw;
  height: 56px;
  background-color: #202124;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 0px 25px;
}

header nav ul {
  display: flex;
  gap: 25px;
}

header nav ul li {
  list-style: none;
}

header nav ul li a {
  color: white;
  text-decoration: none;
}

.logo {
  font-size: 1.6rem;
}

.nav-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.hamburger {
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
}

.profile {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1.5px solid rgb(0, 244, 0);
  background-size: cover;
  cursor: pointer;
  background-position: center;
  background-image: url("../public/android-chrome-512x512.png");
}

.flex {
  display: flex;
  align-items: center;
}

aside {
  height: calc(100% - 56px);
  width: 300px;
  position: fixed;
  top: 56px;
  left: -100%;
  bottom: 0;
  background: #000;
  z-index: 1;
  transition: 0.4s ease-out;
}

.hero{
  padding-top: 56px;
  width: 100%;
  height: 100vh;
  background-color: #202124;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5rem;
  color: white;
}

@media (max-width:800px) {
  header nav ul {
    display: none;
  }
}